  changeArrayObject(productsAfterChange, projectData){
    let arayObjectsAfterFilter = projectData.filter(c => c.equipmentElementType != productsAfterChange[0].equipmentElementType);
    for(let i=0; i<productsAfterChange.length; ++i){
      let itemElement = productsAfterChange[i];
      arayObjectsAfterFilter.push(itemElement);
    }
    return arayObjectsAfterFilter;
  }

  changeСomplexArrayObject(productsAfterChange, projectData){
    if(productsAfterChange.length == 0){
      return;
    } else {
      let arayObjectsAfterFilter = projectData.filter(c => c.equipmentElementType != productsAfterChange[0].equipmentElementType && c.cloneEquipmentTag != productsAfterChange[0].equipmentElementType);
      //console.log(arayObjectsAfterFilter);
      for(let i=0; i<productsAfterChange.length; ++i){
        let itemElement = productsAfterChange[i];
        arayObjectsAfterFilter.push(itemElement);
      }
      //console.log(arayObjectsAfterFilter);
      return arayObjectsAfterFilter;
    }
  }

  changeArrayIoPerCard(productsAfterChange, projectData){
    if(productsAfterChange.length == 0){
      return;
    } else {
      let arayObjectsAfterFilter = projectData.filter(c => c.equipmentElementType != productsAfterChange[0].equipmentElementType && c.equipmentModel != productsAfterChange[0].equipmentModel);
      console.log(arayObjectsAfterFilter);
      for(let i=0; i<productsAfterChange.length; ++i){
        let itemElement = productsAfterChange[i];
        arayObjectsAfterFilter.push(itemElement);
      }
      console.log(arayObjectsAfterFilter);
    return arayObjectsAfterFilter;
    }
  }


