import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ControllerService } from '../../services/controller.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

//import * as XLSX from 'xlsx';
//import {saveAs as importedSaveAs} from "file-saver";
import { ExcelService } from '../../services/excel.service';
import { ProjectService } from '../../services/project.service';


@Component({
  selector: 'app-controllers-list',
  templateUrl: './controllers-list.component.html',
  styleUrls: ['./controllers-list.component.css']
})
export class ControllersListComponent implements OnInit {

  projectId: String;
  controllers: any;
  controller: any;
  controllersArrayList: any;
  isChecked: Boolean = false;
  projectName: any;

  constructor(
    private controllerService: ControllerService,
    private router: Router,
    private route: ActivatedRoute,
    private spinnerService: Ng4LoadingSpinnerService,
    private projectServise: ProjectService,
    private excelService: ExcelService,
  ) {
      this.projectId = this.route.snapshot.params['id'];
      this.controllersArrayList = [];
    }

  ngOnInit() {
    this.spinnerService.show();
    this.controllerService.getControllersList(this.projectId).subscribe(controllerList => {
      this.controllers = controllerList;
      console.log(this.controllers);
    },
    err => {
      console.log(err);
      return false;
    });
    this.projectServise.getProjectNameById(this.projectId).subscribe(project => {
      this.projectName = project;
      //console.log(this.projectName);
    }, err => {
      this.spinnerService.hide();
      console.log(err);
      return false;
    });
    this.spinnerService.hide();
  }

  pushToControllerArray(element){
    if(!element.isChecked){
      this.controllersArrayList.push(element._id);
      //console.log(this.instrumentationArrayList);
    } else {
      this.controllersArrayList.pop();
    }
  }

  changeFlag(isCheck){
    if(isCheck){
      this.isChecked = false;
      for(let i=0; i<this.controllers.controllers.length; ++i){
        this.controllers.controllers[i].isChecked = false;
        this.controllersArrayList = [];
      }
    } else {
      this.isChecked = true;
      for(let i=0; i<this.controllers.controllers.length; ++i){
        this.controllers.controllers[i].isChecked = true;
        this.controllersArrayList.push(this.controllers.controllers[i]._id);
      }
    }
  }

  saveToExcell(){
    var wscols = [
      //A       //B       //C       //D       //E       //F       //G       //H       //I       //J       //K     //L       //M       //N     //O       //P
      {wch:5}, {wch:25}, {wch:32}, {wch:17}, {wch:27}, {wch:22}, {wch:20}, {wch:17}, {wch:22}, {wch:18}, {wch:7}, {wch:7}, {wch:7}, {wch:7}, {wch:10}, {wch:12},
      //Q       //R       //S       //T       //U       //V       //W         //X     //Y
      {wch:12}, {wch:15}, {wch:12}, {wch:10}, {wch:10}, {wch:17}, {wch:14}, {wch:15}, {wch:22}
    ];
    let projectArray = [];
    for(let i=0; i<this.controllers.controllers.length; ++i){
      //console.log(this.instrumentation[i]._id);
      projectArray.push(this.controllers.controllers[i]._id)
    }
    let queryString = 'controllers-item-list';
    const scheetName = " Controllers List";
    const controllerName = 'Controllers';
    const fileName = "CONTROLLER INDEX";
    this.excelService.exportToExcell(this.projectId, this.projectName.title, projectArray, queryString, scheetName, fileName, controllerName, wscols);
    /*
    var ws = XLSX.utils.table_to_book(document.getElementById('exportable')).Sheets.Sheet1;
    ws['!cols'] = wscols;
    let wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Controller Shedule List");
    let wbout = XLSX.write(wb, {
      bookType:'xlsx',
      type:'binary',
      cellStyles:true
      //bookSST:true
    });
    console.log(wbout);
    function s2ab(s) {
      let buf = new ArrayBuffer(s.length);
      let view = new Uint8Array(buf);
      for (let i=0; i!=s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
      return buf;
    }
    importedSaveAs(new Blob([s2ab(wbout)],{type:"application/octet-stream"}), "Report from project controllers"+Date.now()+".xlsx");
    */
  }

  saveToExcellUIData(){
    var wscols = [
      //A       //B       //C       //D       //E       //F       //G       //H       //I       //J       //K       //L       //M       //N       //O       //P
      {wch:14}, {wch:10}, {wch:22}, {wch:28}, {wch:28}, {wch:32}, {wch:12}, {wch:18}, {wch:18}, {wch:21}, {wch:15}, {wch:17}, {wch:19}, {wch:27}, {wch:19}, {wch:22},
      //Q       //R       //S       //T       //U       //V       //W         //X     //Y     //Z       //AA      //AB      //AC      //AD      //AE      //AF
      {wch:19}, {wch:8}, {wch:8}, {wch:7}, {wch:7}, {wch:13}, {wch:12}, {wch:15}, {wch:13}, {wch:13}, {wch:9}, {wch:12}, {wch:16}, {wch:15}, {wch:15}, {wch:22},
      //AG      //AH
      {wch:16}, {wch:8}
    ];
    let projectArray = [];
    for(let i=0; i<this.controllers.controllers.length; ++i){
      //console.log(this.instrumentation[i]._id);
      projectArray.push(this.controllers.controllers[i]._id)
    }
    const queryString = 'controllers-item';
    const scheetName = "Controllers UI List";
    const controllerName = 'Controllers';
    const fileName = "CONTROLLER REPORT";
    this.excelService.exportToExcell(this.projectId, this.projectName.title, projectArray, queryString, scheetName, fileName, controllerName, wscols);
  }

  saveController() {
    this.controller = {};
    this.controller.length = 0;
    this.spinnerService.show();
    this.controllerService.createController(this.projectId, this.controller).subscribe(
      (res: Array<string>) => {
        let id = res['_id'];
        let responseId = res[res.length-1]['_id']
        let routeToControllerItem = '/project/'+this.route.snapshot.params['id']+'/controllers/'+responseId;
        this.router.navigate([routeToControllerItem]);
      }, (err) => {
        console.log(err);
      }
    );
    this.spinnerService.hide();
  }

}



